import React from 'react'
import { useState } from 'react'

const Form = ({passUpState}) => {

    const [userInput, setUserInput] = useState('')

    const submit = (e) => {
        e.preventDefault()

        passUpState(userInput)
    }

    return (
        <form className="search-and-submit container">
            <input type='text' placeholder="enter a movie or tv show" value={userInput} onChange={(e) => setUserInput(e.target.value)}/>
            <button onClick={submit} className="submit-btn" type="submit">Search</button>
        </form>
    )
}

export default Form
