import './App.css';
import Form from './components/Form';
import Main from './components/Main';
import Modal from './components/Modal';
import { useState } from 'react';
import axios from 'axios';

function App() {

  const [data, setData] = useState('')
  const [showModal, setShowModal] = useState(false)

  const globalUserInput = (value) => {

    axios.get(`https://www.omdbapi.com/?t=${value}&apikey=9fabedf7`)
    .then(res => {
      setData([res.data])
    })
  }

  //Open Modal
  const openModal = () => {
    setShowModal(!showModal)
  }

  //Close Modal
  const closeModal = () => {
    setShowModal(false)
  }

  return (
    <div className="App">
      <Form passUpState={globalUserInput}/>
      {data.length > 0 ? <Main image={data[0].Poster} title={data[0].Title} desc={data[0].Plot} year={data[0].Year} actors={data[0].Actors} rated={data[0].Rated} openModal={openModal}/> : ''}
      {showModal && <Modal image={data[0].Poster} title={data[0].Title} genre={data[0].Genre} type={data[0].Type} rated={data[0].Rated} runtime={data[0].Runtime} showModal={showModal} closeModal={closeModal}/>}
    </div>
  );
}

export default App;
