import React from 'react'

const Modal = ({image, title, genre, type, rated, runtime, showModal, closeModal}) => {
    return (
        <>
        <section className={`modal ${showModal ? 'active' : ''}`}>
        <div className="modal-heading"><i onClick={closeModal} className="fas fa-times close-btn"></i></div>
            <img src={image} alt="" className="modal-image"/>
            <div className="modal-text">
                <div className="modal-title">
                    <h1>{title}</h1>
                </div>
            <div className="modal-info">
                <p className="genre">{genre}</p>
                <p className="type">{type}</p>
                <p className="rated">{rated}</p>
                <p className="runtime">{runtime}</p>
            </div>
        </div>
    </section>
    <div className={`overlay ${showModal ? 'active' : ''}`}></div>
    </>
    )
}

export default Modal
