import React from 'react'

const Main = ({image, title, desc, year, actors, rated, openModal}) => {
    return (
        <section id="movie-card">
        <p className="error-message"></p>
        <img src={image} alt="" id="movie-image"/>
        <h1 className="movie-title">{title}</h1>
        <p className="movie-desc">{desc}</p>

        <div className="extra-info">
            <p id="year">{year}</p>
            <p id="actors">{actors}</p>
            <p id="rating">{rated}</p>
        </div>
        <div className="btn-container">
        <button onClick={openModal} className="read-more">Read More</button>
        </div>
    </section>
    )
}

export default Main
